import { NextPage } from 'next';
import { ethers } from 'ethers';

const test = async () => {

const provider = new ethers.providers.JsonRpcProvider('https://mainnet.infura.io/v3/ab962fb32dfc49a8ab6ab72a6a318c85')

let contractAddress = "0xD275B1550E8ca8Da84c552ACa9313Ec4a5B9bD07";

let topic = ethers.utils.id("transferFrom(address,address,uint256)");

let filter = {
    address: contractAddress,
    fromBlock: 8960961,
    toBlock: "latest",
    topics: [ topic ]
}

provider.getLogs(filter).then((result) => {
    console.log("result ", result);

    for (let entry of result) {
      
      console.log("entry ", entry)
  }
});
}

const Home: NextPage<{ userAgent: string }> = ({ userAgent }) => {
  test()
  return (
    <h1>Hello world! - user agent: {userAgent}</h1>
  )
};

Home.getInitialProps = async ({ req }) => {
  const userAgent = req ? req.headers['user-agent'] || '' : navigator.userAgent;
  return { userAgent };
};

export default Home;

